footer {
    margin-top: 10vmax;
    padding: 2vmax;
    background-color: rgb(34, 33, 33);
    color: white;
    display: flex;
    align-items: center;
}

.leftFooter {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.leftFooter>h4 {
    font-family: "Roboto";
    font-size: 1vmax;
}

.leftFooter>p {
    text-align: center;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    font-size: 1.2vmax;
}

.leftFooter>img {
    width: 10vmax;
    margin: 1vmax;
    cursor: pointer;
}

.midFooter {
    width: 60%;
    text-align: center;
}

.midFooter>h1 {
    font-size: 4vmax;
    font-family: "Roboto";
    color: #eb4034
}

.midFooter>p {
    max-width: 60%;
    margin: 1vmax auto;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.rightFooter {
    width: 20%;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.rightFooter>h4 {
    font-family: "Roboto";
    font-size: 1.4vmax;
    text-decoration: underline;
}

.rightFooter>a {
    text-decoration: none;
    font-size: 1.3vmax;
    margin: .5vmax;
    color: white;
    transition: all 0.5s;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;

}

.rightFooter>a:hover {
    color: #eb4034;
}

@media screen and (max-width: 600px) {

    footer {
        margin-top: 10vmax;
        padding: 2vmax;
        background-color: rgb(34, 33, 33);
        color: white;
        display: flex;
        align-items: center;
    }

    .leftFooter {
        width: 20%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .leftFooter>h4 {
        font-family: "Roboto";
        font-size: 0.91vmax;
        padding-bottom: 1vmax;
    }

    .leftFooter>p {
        text-align: center;
        font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
        font-size: 0.9vmax;
    }

    .leftFooter>img {
        width: 10vmax;
        margin: 1vmax;
        cursor: pointer;
    }

    .midFooter {
        width: 60%;
        text-align: center;
    }

    .midFooter>h1 {
        font-size: 2vmax;
        font-family: "Roboto";
        color: #eb4034
    }

    .midFooter>p {
        font-size: 1.5vmax;
        margin: 1vmax auto;
        font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
        padding-top: 2vmax;
    }

    .midFooter>p1 {

        font-size: 1vmax;
        margin: 1vmax auto;
        font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    }

    .rightFooter {
        width: 20%;

        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .rightFooter>h4 {
        font-family: "Roboto";
        font-size: 1.4vmax;
        text-decoration: underline;
        padding-bottom: 2vmax;
    }

    .rightFooter>a {
        text-decoration: none;
        font-size: 1.3vmax;
        margin: .5vmax;
        color: white;
        transition: all 0.5s;
        font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;

    }

    .rightFooter>a:hover {
        color: #eb4034;
    }
}