.sidebar {
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    padding: 4rem 0;
}

.sidebar>a:first-child {
    padding: 0;
}

.sidebar>a>img {
    width: 100%;
    transition: all 0.5s;
}

.sidebar>a>img:hover {
    filter: drop-shadow(0 0 10px tomato);
}

.sidebar a {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.493);
    font: 400 1rem "Roboto";
    padding: 2rem;
    transition: all 0.5s;
}

.sidebar a:hover {
    color: tomato;
    transform: scale(1.1);
}

.sidebar a>P {
    display: flex;
    align-items: center;
}

.sidebar a>p>svg {
    margin-right: 0.5rem;
}

.MuiTypography-root {
    background-color: #fff !important;
}

@media screen and (max-width: 600px) {
    .sidebar {
        background-color: rgb(255, 255, 255);
        display: flex;
        flex-direction: row;
        padding: 4rem 0;

    }

    .sidebar>a:first-child {
        text-decoration: none;
        color: rgba(0, 0, 0, 0.493);
        font: 600 1vmax "Roboto";
        padding: 1rem;
        transition: all 0.5s;
    }

    .sidebar>a>img {
        width: 100%;
        transition: all 0.5s;
    }

    .sidebar>a>img:hover {
        filter: drop-shadow(0 0 10px tomato);
    }

    .sidebar a {
        text-decoration: none;
        color: rgba(0, 0, 0, 0.7);
        font: 600 1.8vmax "Roboto";
        padding: 1rem;
        transition: all 0.5s;
    }


    .sidebar a:hover {
        color: tomato;
        transform: scale(1.1);
    }

    .sidebar a>P {
        display: flex;
        align-items: center;
    }

    .sidebar a>p>svg {
        margin-right: 0.5rem;
    }

    .MuiTypography-root {
        background-color: #fff !important;
    }
}